/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Granular Development Blog","author":"Brian Smith"}},"markdownRemark":{"id":"/Users/briansmith/Documents/Dev/granular-blog/src/pages/post-9-29-2019 /index.md absPath of file >>> MarkdownRemark","html":"<h3>How to delete a specific parent from a table and all its children in another table.</h3>\n<p>SQL tables</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">| Parent      |\n| ----------- |\n| id          |\n\n| Child                     |\n| ------------------------- |\n| id                        |\n| parent_id (foreign key)   |</code></pre>\n      </div>\n<p>In order to remove a parent from the parents table and all its child, requires you to solve violation on foreign key constraints. We can solve this by cascading.</p>\n<h3>Solution</h3>\n<p>   monitor_id = db.Column(db.Integer, db.ForeignKey(‘monitors.id’))</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">class Parent(db.Model):\n    __tablename__ = &#39;parent&#39;\n    id = db.Column(db.Integer, primary_key=True)\n    child = relationship(Child, cascade=&quot;all,delete&quot;, backref=&quot;parent&quot;)\n\nclass Child(db.Model):\n    __tablename__ = &#39;child&#39;\n    id = db.Column(db.Integer, primary_key=True)\n    parent_id = db.Column(db.Integer, db.ForeignKey(&#39;parent.id&#39;))</code></pre>\n      </div>","frontmatter":{"title":"SQLAlchemy update or delete on table violates foreign key constraint","date":"September 29, 2019"}}},"pathContext":{"slug":"/post-9-29-2019 /","previous":{"fields":{"slug":"/post-8-22-2019/"},"frontmatter":{"title":"Nginx Loadbalancing Passing Path Parameters"}},"next":false}}

/***/ })

